export default {
  props: {
    changehandler: {
      type: Function,
      required: true
    },

    selectatsubs: {
      type: Function,
      required: true
    },

    selectedsubsdate: {
      type: String,
      required: true
    },

    selecteddate: {
      type: Array,
      required: true
    },

    products: {
      type: Boolean,
      required: true
    }
  },

  data: () => ({
    catalog: []
  }),

  watch: {
    products (newval, oldval) {
      this.merge_items()
    }
  },

  mounted() {
    this.merge_items()
  },

  methods: {
    merge_items() {
      const list_product = this.$store.state.products
      const merged_items = list_product.map(item => {
        const filtered = this.selecteddate.filter(f_item => f_item.qty > 0 && item.id == f_item.id)

        if (filtered.length) {
          return filtered[0]
        }

        return {
          SKU: item.SKU,
          detail_id: item.detail[0].detail_id,
          discount_price: item.detail[0].discount_price,
          id: item.id,
          main_image: item.detail[0].main_image,
          name: item.name,
          normal_price: item.detail[0].normal_price,
          product_id: item.id,
          qty: item.qty,
          select_date: false,
          variant: item.detail[0].variant
        }
      })

      this.catalog = merged_items
    }
  }
}
